.img-fill{
  width: 100%;
  display: block;
  overflow: hidden;
  position: relative;
  text-align: center
}

.img-fill img {
  /* min-height: 100%;
  min-width: 100%; */
  position: relative;
  display: inline-block;
  /* max-width: none; */
}

.blocks-box,
.slick-slider {
  margin: 0;
  padding: 0 !important;
}

.slick-slide {
  float: left;
  padding: 0;
}

.Modern-Slider .item .img-fill .info {
  position:absolute;
  width:100%;
  height:100%;
  top:0px;
  left:0px;
  background:rgba(0,0,0,.50);
  text-align:center;
}

.Modern-Slider .item .img-fill img {
  filter:blur(5px);
}

.Modern-Slider .item .info > div {
  /* @include center(y); */
  position: absolute;
  top: percentage(421 / 770);
  text-align: left;
  right: percentage(400 / 1920);

  span {
    color: #000;
    font-family: $primary-font;
    font-size: 3rem;
    background: #009688;
    display: inline-block;
    font-weight: 600;
    padding: 0.8em 1.5em 0.8em 0.6666666666666666em;
    /* padding: 24px 45px 24px 20px; */

    &:last-child {
      padding-top: 0.3333333333333333em;
      margin-top: -1.1666666666666667em;
      /* padding-top: 10px; */
      /* margin-top: -35px; */
    }
  }
}

.Modern-Slider .NextArrow {
  position:absolute;
  top:50%;
  right:0px;
  width:45px;
  height:45px;
  background:rgba(0,0,0,.50);
  border:0 none;
  margin-top:-22.5px;
  text-align:center;
  color:#FFF;
  z-index:5;
}

.Modern-Slider .NextArrow:before {
  content:'\f105';
}

.Modern-Slider .PrevArrow {
  position:absolute;
  top:50%;
  left:0px;
  width:45px;
  height:45px;
  background:rgba(0,0,0,.50);
  border:0 none;
  margin-top:-22.5px;
  text-align:center;
  color:#FFF;
  z-index:5;
}

.Modern-Slider .PrevArrow:before {
  content:'\f104';
}

.Modern-Slider {
  background:#000;
}
